@import url(https://fonts.googleapis.com/css?family=Margarine);
html {
  font-family: "Margarine", cursive;
  font-size: 20px; }

body {
  background-color: #44218d;
  background-image: url("https://www.transparenttextures.com/patterns/batthern.png"); }

ul {
  list-style-type: none; }

button {
  border: none;
  background-color: unset;
  font-size: unset;
  font-family: "Margarine";
  cursor: pointer; }
  button:focus {
    outline: none; }

body, h1, h2, h3, p, button, ul {
  margin: 0;
  padding: 0; }

.game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 2em;
  width: 100 .game;
  font-size: 1.2em; }
  .game h1.main-title {
    margin-bottom: 1em;
    color: #e98074; }
  .game .orientation-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 35em; }
    .game .orientation-wrapper .board-and-score {
      text-align: center; }
      .game .orientation-wrapper .board-and-score .game-board {
        display: grid;
        display: -ms-grid;
        grid-template: repeat(3, 4em)/repeat(3, 4em);
        -ms-grid-columns: 4em 4em 4em;
        -ms-grid-rows: 4em 4em 4em;
        -ms-grid-column-span: 3;
        margin-bottom: .5em;
        border: 0.2em solid #d83f87;
        border-radius: .2em;
        box-shadow: 0 0 1em #d83f87;
        background-color: #d83f87; }
        .game .orientation-wrapper .board-and-score .game-board .square {
          display: -webkit-flex;
          display: flex;
          -webkit-align-items: center;
                  align-items: center;
          -webkit-justify-content: center;
                  justify-content: center;
          border: 1px solid #d83f87;
          box-shadow: 0 0 0.2em inset #d83f87;
          color: #e98074;
          background: #2a1b3d;
          font-size: 3em; }
          .game .orientation-wrapper .board-and-score .game-board .square:hover {
            background-color: #44218d; }
        .game .orientation-wrapper .board-and-score .game-board .square1 {
          -ms-grid-column: 1;
          -ms-grid-row: 1; }
        .game .orientation-wrapper .board-and-score .game-board .square2 {
          -ms-grid-column: 2;
          -ms-grid-row: 1; }
        .game .orientation-wrapper .board-and-score .game-board .square3 {
          -ms-grid-column: 3;
          -ms-grid-row: 1; }
        .game .orientation-wrapper .board-and-score .game-board .square4 {
          -ms-grid-column: 1;
          -ms-grid-row: 2; }
        .game .orientation-wrapper .board-and-score .game-board .square5 {
          -ms-grid-column: 2;
          -ms-grid-row: 2; }
        .game .orientation-wrapper .board-and-score .game-board .square6 {
          -ms-grid-column: 3;
          -ms-grid-row: 2; }
        .game .orientation-wrapper .board-and-score .game-board .square7 {
          -ms-grid-column: 1;
          -ms-grid-row: 3; }
        .game .orientation-wrapper .board-and-score .game-board .square8 {
          -ms-grid-column: 2;
          -ms-grid-row: 3; }
        .game .orientation-wrapper .board-and-score .game-board .square9 {
          -ms-grid-column: 3;
          -ms-grid-row: 3; }
      .game .orientation-wrapper .board-and-score h3.next-player {
        margin-bottom: 1em;
        color: #e98074; }
    .game .orientation-wrapper ul.history-list {
      -webkit-align-self: flex-start;
              align-self: flex-start;
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-around;
              justify-content: space-around;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap;
      width: 16em;
      margin-bottom: 2em; }
      .game .orientation-wrapper ul.history-list li.history-item {
        margin: .5em 0;
        font-size: .7em; }
        .game .orientation-wrapper ul.history-list li.history-item button.jump-button {
          border: 0.2em solid #e98074;
          border-radius: .5em;
          box-shadow: 0 0 0.5em inset #e98074, 0 0 0.5em #e98074;
          padding: .5em 0;
          width: 10em;
          color: #d83f87;
          background-color: #2a1b3d; }
          .game .orientation-wrapper ul.history-list li.history-item button.jump-button:hover {
            background-color: #44218d; }

